/* Adjust height, width (Global) */
body, html, #root, #slide_container, .slide {
  height: 100%;
  width: 100%;

  font-family: 'Roboto Condensed', sans-serif;
  /* For debugging */
  color: white;
}

#root {
  display: flex;
}

/* Global Heading */
h1 {
  /* font-size: 4rem; */ /* UNCOMMENT */
  font-size: 2.5rem; /* DEL */
}

/* Main body for all slides */
#slide_container {
  background-color: transparent;
}

/* Slide itself */
.slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide_con {
  display: flex;
  flex-direction: row;
  width: 100%;
}

/* Buttons */
#slide_btns {
  position: absolute;
  padding: 20px;
}

.pagination_btn {
  border: 0px;
  color: white;
  background-color: transparent;
  font-size: 1rem;
  margin: 0px;
  padding: 0px;
}

/* Slide 2 */
.container {
  /* border: 2px solid blue; */
  height: 500px;
  width: 100%;
  margin: 50px;
}

.container_2 {
  width: 70%;
}

#graph {
  /* border: 2px solid red; */
  min-height: 500px;
  height: 750px; /* Might not be responsive */
}

#graph h2 {
  margin: 10px 0px 80px 0px;
  font-size: 2.3rem;
}

.date_count {
  margin: 0px !important;
  color: black !important;
  font-size: 2.5rem !important;
}

/* Slide 3 */
#item_box {
  margin-top: 50px; /* Possibly use flex box to position this at bottom instead of margin !!! */
  padding: 20px;
  /* border: 2px solid red; */
  height: 150px;
  display: flex;
  justify-content: center;
}

#random_time {
  margin: 0px;
  margin-left: 5px;
  display: none;
}

.image_liked {
  margin: 10px;
}

/* Slide 5 */
#stats {
  display: flex;
  justify-content: space-around;
}

#stats h4 {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 0px;
}

.stats_con h5 {
  font-size: 2rem;
  margin-top: 10px;
  text-align: center;
}

/* Slide 6 */
#gifts {
  margin-top: 100px;
  display: flex;
  font-size: 1.7rem;
  display: none;
}

.image_gift {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  display: flex;
}

/* Misc */
.click_to {
  font-size: 1.8rem;
  position: absolute;
  right: 50px;
  top: 50px;
  /* display: none; */ /* hidden init */
  z-index: 1000;
}

#bg_container {
  width: 100%;
  height: 100%;
  background-color: black;
  position: absolute;
  z-index: -1000
}

.image_liked {
  width: 125px;
  height: 125px;
}

/* Particle.js */
canvas {
  position: fixed;
  background-color: black;
  z-index: -99;
}
